header {
  display: grid;
  grid-template-areas: "empty title login";
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  white-space: nowrap;
  width: 100%;
}

header > h1 {
  grid-area: title;
  justify-self: center;
}

header > .header-logout {
  grid-area: login;
  display: flex;
  align-items: center;
  justify-self: end;
  gap: 5px;
}

nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

main {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  justify-items: center;
  align-items: center;
  width: 100%;
}

.clips {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 10px;
  width: 80%;
  border: var(--ykspremium) 5px solid;
  padding: 10px;
}

.clip {
  min-width: 200px;
  max-width: 400px;
}

.paginator {
  display: flex;
  flex-flow: row nowrap;
  list-style: none;
  gap: 2px;
}
